html {
  height: 100%; }

body {
  height: 100%; }

.content {
  height: 100%; }

.header__logo {
  margin: 0; }

.services__header {
  font-weight: bold;
  font-style: italic; }

.services__list {
  font-size: 1em;
  margin: 0;
  list-style-type: none;
  text-align: left; }

.services__item {
  line-height: 1; }

.social__list {
  list-style-type: none; }

.social__item {
  display: inline-block; }

.contact__header {
  font-size: 1em;
  font-weight: bold;
  font-style: italic;
  margin: 0; }

.contact > a {
  color: white;
  text-decoration: none;
  border-bottom: solid 0.1em rgba(255, 255, 255, 0.1);
  transition: all ease 0.2s; }

.contact > a:hover {
  color: white;
  text-decoration: none;
  border-bottom: solid 0.1em white; }

/* ==========================================================================
   Non Grid Based Layout
   ========================================================================== */
.content.no-grid {
  position: relative;
  margin: 0 auto;
  display: inline-block; }
  .content.no-grid .header {
    position: relative;
    width: 100%;
    height: 10em; }
  .content.no-grid .header__logo {
    position: absolute;
    height: 4em;
    margin-top: 0; }
  .content.no-grid #main-logo {
    max-width: 4em; }
  .content.no-grid .services {
    position: absolute;
    height: 3em;
    margin-top: 5em;
    margin-left: 8em;
    width: 30em; }
  .content.no-grid .services__header {
    float: left;
    margin-right: 1rem; }
  .content.no-grid .services__header {
    font-size: 3.5em;
    margin: 0;
    font-weight: bold;
    font-style: italic;
    line-height: 1; }
  .content.no-grid .services__list {
    font-size: 1em;
    margin: 0;
    list-style-type: none;
    text-align: left; }
  .content.no-grid .services__item {
    line-height: 1; }
  .content.no-grid .social__icon {
