    text-align: left; }

/* ==========================================================================
   Grid Based Layout
   ========================================================================== */
.content.has-grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr 12.5vh 6.25vh 1fr 25vh 25vh;
  grid-template-areas: ". ." "logo ." ". services" ". ." "social social" ". contact"; }
  .content.has-grid #main-logo {
    max-height: 100%; }
  .content.has-grid .header__logo {
    max-width: 50vw;
    grid-area: logo;
    text-align: right; }
  .content.has-grid .services {
    grid-area: services;
    text-align: left; }
    .content.has-grid .services__header {
      font-size: 7.5vh;
      margin: 0;
      line-height: 1;
      display: inline-block; }
    .content.has-grid .services__list {
      font-size: 2.3vh;
      margin: 0;
      list-style-type: none;
      text-align: left;
      display: inline-block;
      padding: 0 0 0 1em; }
    .content.has-grid .services__item {
      line-height: 1; }
  .content.has-grid .social {
    grid-area: social; }
    .content.has-grid .social__list {
      margin: 0;
      padding: 0; }
    .content.has-grid .social__item {
      padding: 0.2em; }
    .content.has-grid .social img {
      max-height: 6.25vh; }
  .content.has-grid .contact {
    grid-area: contact;
    text-align: left; }

.content.no-prop {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1em 4em auto 1fr 8em 8em;
  grid-template-areas: ". ." "logo logo" "services services" ". ." "social social" "contact contact"; }
  .content.no-prop #main-logo {
    max-height: 100%; }
  .content.no-prop .header__logo {
    max-width: 50vw;
    grid-area: logo;
    align-self: stretch;
    text-align: right; }
  .content.no-prop .services {
    grid-area: services;
    text-align: left; }
    .content.no-prop .services__header {
      font-size: 3.6em;
      margin: 0;
      line-height: 1;
      display: inline-block; }
    .content.no-prop .services__list {
      font-size: 1em;
      margin: 0;
      list-style-type: none;
      text-align: left;
      display: inline-block;
      padding: 0 0 0 1em;
      font-weight: 100; }
    .content.no-prop .services__item {
      line-height: 1; }
  .content.no-prop .social {
    grid-area: social; }
    .content.no-prop .social__list {
      margin: 0;
      padding: 0; }
    .content.no-prop .social__item {
      padding: 0.2em; }
      .content.no-prop .social__item:hover {
